import React from 'react';
import s from './Videos.module.css';

const Videos = (props) => {
    return (
        <div className={s.videoComponent}>
            <div className={s.name}>{props.video.name}</div>
            <div>
                <hr className={s.hr}/>
            </div>
            <div className={s.videosContent}>
                <iframe width={props.video.width} height={props.video.height} src={props.video.src}
                        title={props.video.title} frameBorder={props.video.frameBorder}
                        allow={props.video.allow}
                        allowFullScreen></iframe>
            </div>
        </div>
    );
};

export default Videos;